import React from 'react';
import { useConditionalField } from './conditionalForm';
import { SelectInput, SelectInputProps } from './selectInput';
import { useFieldError } from './useFieldError';
import { useMemoizedField } from './useMemoizedField';

export type SelectFieldProps<T = any> = Omit<
  SelectInputProps<T>,
  'name' | 'value' | 'onChange' | 'error'
> & {
  /**
   * Field name
   */
  readonly name: string;
};

/**
 * Material-UI Select with Formik support.
 */
export function SelectField<T>(props: SelectFieldProps<T>) {
  const { name } = props;

  useConditionalField(props.name, 'SelectField');

  const [field, , { setValue, setTouched }] = useMemoizedField(name);
  const error = useFieldError(name);

  return (
    <SelectInput
      {...props}
      value={field.value}
      onChange={setValue}
      onBlur={event => {
        setTouched(true);
        props.onBlur?.(event);
      }}
      error={error != null}
      helperText={error ?? props.helperText}
    />
  );
}
